*{
    font-family: Arial, Helvetica, sans-serif;
}

body{
    padding: 60px;
    font-size: 1.5rem;
    text-align: center;
}

input{
    width: 50%;
    font-size: 1.2rem;
    color: grey;
    text-align: center;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}